;AAmnBR;;AArnBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AAynBR;;AA3nBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AA+nBR;;AAjoBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AAqoBR;;AAvoBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AA2oBR;;AA7oBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AAipBR;;AAnpBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AAupBR;;AAzpBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AA6pBR;;AA/pBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AAmqBR;;AArqBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AAyqBR;;AA3qBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AA+qBR;;AAjrBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AAqrBR;;AAvrBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AA2rBR;;AA7rBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AAisBR;;AAnsBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AAusBR;;AAzsBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AA6sBR;;AA/sBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AAmtBR;;AArtBI;EA7PA,yBAwPY;EAvPZ,WAAA;EA8PI,gBAAA;AAytBR;;AArtBA;EACI,gBAAA;EACA,eAAA;EACA,kBAAA;AAwtBJ;;AArtBA;EACI,eAAA;AAwtBJ","sourcesContent":["\n    $env-matrix-size: 8;\n    $env-play-sensitivity: 0.1;\n    $env-tile-size: 60;\n    $env_tile_margin: 5;\n    $env-background-enabled: false;\n    $env-background-image: none;\n    \n@use \"sass:list\";\n@use \"sass:math\";\n@use \"sass:string\";\n\n$tile-size: 80px !default;\n$tile-margin: 5px !default;\n$app-background-enabled: false;\n$app-background-image: url('background.jpg');\n\n@import 'themes/default.theme.scss';\n//@import 'themes/tile-theme3.scss';\n\n@function textcolor($color) {\n    @if red($color) * 0.299 + green($color) * 0.587 + blue($color) * 0.114>150 {\n        @return #000;\n    } @else {\n        @return #fff;\n    }\n}\n\n@mixin tileStyle($color) {\n    background-color: $color;\n    color: textcolor($color);\n}\n\n$tile-size: $env-tile-size + 0px;\n$tile-margin: $env-tile-margin + 0px;\n$board-size: $env-matrix-size;\n$app-background-enabled: $env-background-enabled;\n$app-background-image: $env-background-image;\n$action-height: 44px;\n\n$app-size: ($tile-size * $board-size) + $tile-margin * ($board-size + 1);\n\nhtml,\nbody {\n    height: 100%;\n    width: 100%;\n    margin: 0px;\n    padding: 0px;\n}\n\nbody {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    background-color: $game-background;\n    font-family: Verdana, sans-serif;\n    color: textcolor($game-background);\n}\n\n.action {\n    display: flex;\n    width: $app-size;\n    height: $action-height;\n    justify-content: space-between;\n    align-content: center;\n\n    .btn-container {\n        display: flex;\n        align-items: center;\n        $action-padding: 3px;\n        $action-size: 20px + 2 * $action-padding;\n\n        .refresh-count {\n            position: absolute;\n            text-align: center;\n            line-height: 20px;\n            top: -9px;\n            left: -9px;\n            font-size: 18px;\n            height: 20px;\n            width: 20px;\n            border-radius: 50%;\n            background-color: white;\n            box-shadow: $placeholder-shadow;\n            color: #000;\n        }\n\n        .btn {\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            border-radius: $tile-border-radius;\n            padding: $action-padding;\n            height: $action-size;\n            width: $action-size;\n            box-shadow: $placeholder-shadow;\n            margin-left: 10px;\n            cursor: pointer;\n            transform: scale(1);\n            transition: transform 0.1s;\n\n            &:hover {\n                transform: scale(1.2);\n            }\n\n            .icon {\n                height: 20px;\n                width: 20px;\n\n                svg {\n                    height: 100%;\n                    width: 100%;\n                }\n            }\n\n            &.refresh {\n                svg {\n                    fill: red;\n                }\n            }\n\n            &.back {\n                position: relative;\n\n                svg {\n                    fill: green;\n                }\n            }\n        }\n    }\n\n    .score-container {\n        display: flex;\n        align-items: flex-end;\n        flex-direction: column;\n\n        .score {\n            font-size: 26px;\n            display: flex;\n            align-items: center;\n            flex-direction: row-reverse;\n        }\n    }\n}\n\n@keyframes newtile {\n    0% {\n        transform: scale(0.5);\n    }\n\n    100% {\n        transform: scale(1);\n    }\n}\n\n@keyframes movetile {\n    0% {\n        transform: scale(1);\n    }\n\n    40% {\n        transform: scale(1.2);\n    }\n\n    100% {\n        transform: scale(1);\n    }\n}\n\n.movetile {\n    animation-name: movetile;\n    animation-duration: 0.3s;\n    animation-delay: 0.2s;\n}\n\n.newtile {\n    animation-name: newtile;\n    animation-duration: 0.15s;\n}\n\n.app {\n    box-shadow: $app-shadow;\n    border-radius: $app-border-radius;\n    min-height: $app-size;\n    min-width: $app-size;\n    position: relative;\n    background-color: $app-background;\n\n    .game-over {\n        height: $app-size;\n        width: $app-size;\n        position: absolute;\n        top: 0px;\n        left: 0px;\n        display: none;\n        align-items: center;\n        justify-content: center;\n        font-size: 40px;\n        background-color: rgba(255, 255, 255, 0.6);\n        border-radius: $app-border-radius;\n        z-index: 10;\n    }\n\n    .sqr-row {\n        display: flex;\n\n        &:last-child {\n            margin-bottom: $tile-margin;\n        }\n\n        .sqr {\n            box-sizing: border-box;\n            height: $tile-size;\n            width: $tile-size;\n            background-color: $placeholder-background;\n            @if $app-background-enabled {\n                background-image: $app-background-image;\n            }\n            background-repeat: no-repeat;\n            border-radius: $tile-border-radius;\n            margin-left: $tile-margin;\n            margin-top: $tile-margin;\n            box-shadow: $placeholder-shadow;\n\n            &:last-child {\n                margin-right: $tile-margin;\n            }\n        }\n    }\n\n    @for $i from 1 through $board-size {\n        @for $j from 1 through $board-size {\n            .sqr#{$i}-#{$j} {\n                background-position: $tile-margin -\n                    ($tile-margin + ($j - 1) * ($tile-size + $tile-margin))\n                    $tile-margin -\n                    ($tile-margin + ($i - 1) * ($tile-size + $tile-margin));\n            }\n        }\n    }\n\n    .tile-container {\n        position: absolute;\n        top: 0px;\n        left: 0px;\n\n        .tile {\n            height: $tile-size;\n            width: $tile-size;\n            border-radius: $tile-border-radius;\n            position: absolute;\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            transition: top #{$env-play-sensitivity}s,\n                left #{$env-play-sensitivity}s;\n        }\n\n        @for $i from 1 through $board-size {\n            @for $j from 1 through $board-size {\n                .f#{$i}-#{$j} {\n                    top: $tile-margin + ($i - 1) * ($tile-size + $tile-margin);\n                    left: $tile-margin + ($j - 1) * ($tile-size + $tile-margin);\n                }\n            }\n        }\n    }\n}\n\n@for $i from 0 through ($board-size * $board-size) {\n    $tile-value: math.pow(2, $i);\n    $value-width: string.length(#{$tile-value});\n    $list-length: list.length($tile-color);\n    $color: #000;\n\n    @if $i>=$list-length {\n        $color: list.nth($tile-color, -1);\n    } @else {\n        $color: list.nth($tile-color, $i + 1);\n    }\n\n    .t#{$i} {\n        @include tileStyle($color);\n        font-size: 44px - ($value-width * 4);\n    }\n}\n\n.credits {\n    margin-top: 10px;\n    font-size: 14px;\n    text-align: center;\n}\n\n.heading {\n    font-size: 50px;\n}\n","$tile-color: (\n    #ffe6b3,\n    #ffdc98,\n    #ffd57f,\n    #ffcc66,\n    #ff894c,\n    #fe0000,\n    #ff3334,\n    #ff4d4d,\n    #ff6766,\n    #ff999a,\n    #87ff4d,\n    #62ff1a,\n    #51ff00,\n    #00ff01,\n    #00ffab,\n    #66cbff,\n    #1865ff\n);\n\n$game-background: #fffdd0;\n$app-background: #aaa;\n$placeholder-background: #ddd;\n$placeholder-shadow: none;\n$app-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);\n\n$app-border-radius: 5px;\n$tile-border-radius: 5px;\n"],"sourceRoot":""}]);
